.marqueeSection {
    width: 100vw;
    min-width: 100vw;
    max-width: 100vw;
    height: 90px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(90deg, #268fff 0%, #79c5ff 100%);
    box-shadow: 0 2px 12px rgba(0,0,0,0.08);
    margin: 0;
    padding: 0;
    position: relative;
    overflow: hidden;
}

.marqueeWrapper {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.marquee {
    font-size: 2.1rem;
    font-weight: bold;
    color: #fff;
    letter-spacing: 2px;
    white-space: nowrap;
    animation: marquee-move 12s linear infinite;
}

@keyframes marquee-move {
    0% { transform: translateX(100%); }
    100% { transform: translateX(-100%); }
}
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    background-color: var(--page-bg);
    margin: 0;
    padding: 0;
    width: 100%;
    overflow-x: hidden;
    position: relative;
}

.particleContainer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    pointer-events: none;
}

.title {
    min-height: 3em;
    color: rgb(255, 255, 255);
    font-size: 30px;
    margin-top: 50px;
    text-align: center;
}

.hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 1200px;
    padding: 20px;
}

.text{
    color: #ffffff;
    font-size: 18px;
}
.welcomeText {
  font-size: 24px;
}

.welcomeWrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 140px;
  padding-top: var(--header-height, 70px);
  box-sizing: border-box;
  position: relative;
  z-index: 2;
  transition: height 400ms ease, padding-top 400ms ease;
}

/* ========== RWD 響應式設計 ========== */

/* 平板尺寸 (768px - 1024px) */
@media (max-width: 1024px) {
  .marqueeSection {
    height: 75px;
  }
  
  .marquee {
    font-size: 1.7rem;
    letter-spacing: 1.5px;
  }
  
  .title {
    font-size: 26px;
    margin-top: 40px;
  }
  
  .welcomeText {
    font-size: 20px;
  }
  
  .text {
    font-size: 16px;
  }
  
  .hero {
    padding: 15px;
  }
}

/* 手機尺寸 (< 768px) */
@media (max-width: 768px) {
  .marqueeSection {
    height: 60px;
  }
  
  .marquee {
    font-size: 1.3rem;
    letter-spacing: 1px;
  }
  
  .title {
    font-size: 22px;
    margin-top: 30px;
    min-height: 2.5em;
  }
  
  .welcomeText {
    font-size: 18px;
  }
  
  .text {
    font-size: 15px;
  }
  
  .hero {
    padding: 10px;
  }
  
  .welcomeWrapper {
    height: 120px;
    padding-top: 60px;
  }
}

/* 極小螢幕 (< 480px) */
@media (max-width: 480px) {
  .marqueeSection {
    height: 50px;
  }
  
  .marquee {
    font-size: 1.1rem;
    letter-spacing: 0.5px;
  }
  
  .title {
    font-size: 20px;
    margin-top: 25px;
    min-height: 2em;
  }
  
  .welcomeText {
    font-size: 16px;
  }
  
  .text {
    font-size: 14px;
  }
  
  .hero {
    padding: 10px 5px;
  }
  
  .welcomeWrapper {
    height: 100px;
    padding-top: 55px;
  }
}
